<template>
  <el-card style="max-width: 100%; margin-bottom: 20px;">
    <div slot="header">
      <h3>📋 文生图 提示词</h3>
      <!-- 搜索框 -->
      <div class="search-container">
        <el-input
            v-model="searchKeyword"
            placeholder="搜索提示词标题..."
            clearable
            style="width: 300px; margin-top: 10px;"
            @input="handleSearch"
        >
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input>
      </div>

      <!-- 分类标签选择 -->
      <div class="category-tags-container">
        <el-tag
            v-for="category in categories"
            :key="category.value"
            :type="selectedCategory === category.value ? 'primary' : ''"
            :effect="selectedCategory === category.value ? 'dark' : 'light'"
            class="category-tag"
            @click="selectCategory(category.value)"
            :class="{ 'is-selected': selectedCategory === category.value }"
        >
          {{ category.label }}
        </el-tag>
      </div>
    </div>
    <el-row :gutter="20">
      <!-- 使用虚拟滚动或分页来实现懒加载 -->
      <!-- 这里使用分页实现懒加载效果 -->
      <el-pagination
          v-if="filteredPromptsData.length > 0"
          background
          layout="prev, pager, next"
          :total="filteredPromptsData.length"
          :page-size="pageSize"
          :current-page="currentPage"
          @current-change="handlePageChange"
          style="margin-bottom: 20px; text-align: center;"
      >
      </el-pagination>

      <!-- 显示当前页的数据 -->
      <el-col
          v-for="item in paginatedPromptsData"
          :key="item.id"
          :span="24"
          style="margin-bottom: 20px;"
      >
        <el-card shadow="hover" class="prompt-card" @click.native="showDetailDialog(item)">
          <div slot="header" class="card-header">
            <span class="category-tag-small">{{ item.category }}</span>
            <!-- 动态显示分类 -->
            <span class="card-title">{{ item.title }}</span>
          </div>
          <!-- 使用计算属性控制显示长度 -->
          <p>{{ truncatedPrompt(item.prompt) }}</p>
        </el-card>
      </el-col>

      <!-- 分页控件 -->
      <el-pagination
          v-if="filteredPromptsData.length > pageSize"
          background
          layout="prev, pager, next"
          :total="filteredPromptsData.length"
          :page-size="pageSize"
          :current-page="currentPage"
          @current-change="handlePageChange"
          style="margin-top: 20px; text-align: center;"
      >
      </el-pagination>
    </el-row>

    <!-- 详情弹窗 -->
    <el-dialog
        :title="'提示词详情 - ' + detailItem.title"
        :visible.sync="dialogVisible"
        width="80%"
        :before-close="handleClose"
        class="detail-dialog"
    >
      <div class="detail-content">
        <div class="prompt-section">
          <h4>正面提示词:</h4>
          <p class="prompt-text">{{ detailItem.prompt }}</p>
          <h4>负面提示词:</h4>
          <p class="prompt-text">{{ detailItem.negativePrompt }}</p>
        </div>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button type="success" size="small" @click="copyFullPrompt" icon="el-icon-document-copy" :loading="loading">
          复制
        </el-button>
      </span>
    </el-dialog>

    <!-- 回到顶部按钮 -->
    <el-backtop
        v-show="showBackTop"
        target=".el-card"
        :visibility-height="200"
        :right="50"
        :bottom="50"
    >
      <div style="{
        height: 40px;
        width: 40px;
        border-radius: 50%;
        background-color: #409eff;
        color: white;
        text-align: center;
        line-height: 40px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .09);
      }">
        <i class="el-icon-caret-top"></i>
      </div>
    </el-backtop>
  </el-card>
</template>

<script>
// 导入新的嵌套结构数据
import { textToImagePrompts } from '@/assets/data/textToImagePrompts.js';

export default {
  name: 'TextToImagePage',
  data() {
    return {
      // 保存原始嵌套结构数据
      promptsData: textToImagePrompts,
      // 用于存储当前选择分类下的提示词数组 (过滤后，包含搜索结果)
      filteredPromptsData: [],
      // 用于存储分类选项
      categories: [],
      // 当前选中的分类
      selectedCategory: '',
      // 弹窗可见性
      dialogVisible: false,
      // 当前详情项
      detailItem: {},
      // 复制按钮加载状态
      loading: false,
      // 搜索关键词
      searchKeyword: '',
      // 原始数据（未过滤的），用于搜索
      originalPromptsData: [],
      // 分页相关
      currentPage: 1,
      pageSize: 10, // 每页显示多少条
      // 回到顶部相关
      showBackTop: false,
    };
  },
  computed: {
    // 计算当前页的数据
    paginatedPromptsData() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredPromptsData.slice(start, end);
    }
  },
  methods: {
    // 截断提示词文本
    truncatedPrompt(text) {
      if (!text || text.length <= 20) {
        return text;
      }
      return text.substring(0, 20) + '...';
    },

    // 根据选中的分类过滤数据
    filterPrompts(value) {
      // 获取所有原始数据
      let allPrompts = [];
      Object.values(this.promptsData).forEach(categoryArray => {
        allPrompts = allPrompts.concat(
            categoryArray.map(item => ({
              ...item,
              category: Object.keys(this.promptsData)[Object.values(this.promptsData).indexOf(categoryArray)]
            }))
        );
      });

      // 保存原始数据
      this.originalPromptsData = allPrompts;

      // 根据分类筛选
      let filteredByCategory = [];
      if (value === '') {
        // 如果选择“全部”，则使用所有数据
        filteredByCategory = allPrompts;
      } else {
        // 获取指定分类下的数据
        const categoryArray = this.promptsData[value] || [];
        filteredByCategory = categoryArray.map(item => ({
          ...item,
          category: value
        }));
      }

      // 应用搜索过滤
      this.applySearchFilter(filteredByCategory);
    },

    /**
     * 应用搜索过滤器
     * @param {Array} baseData - 基础数据（已按分类筛选）
     */
    applySearchFilter(baseData) {
      if (!this.searchKeyword.trim()) {
        // 如果搜索关键词为空，则显示所有数据
        this.filteredPromptsData = baseData;
      } else {
        // 模糊搜索标题
        const keyword = this.searchKeyword.toLowerCase().trim();
        this.filteredPromptsData = baseData.filter(item =>
            item.title.toLowerCase().includes(keyword)
        );
      }
      // 重置页码
      this.currentPage = 1;
    },

    /**
     * 处理搜索事件
     */
    handleSearch() {
      // 确保在分类筛选后再应用搜索
      this.filterPrompts(this.selectedCategory);
    },

    /**
     * 选择分类
     * @param {string} categoryValue - 选中的分类值
     */
    selectCategory(categoryValue) {
      this.selectedCategory = categoryValue;
      this.filterPrompts(categoryValue); // 重新筛选
    },

    // 处理分页改变
    handlePageChange(page) {
      this.currentPage = page;
      // 滚动到顶部
      this.scrollToTop();
    },

    // 滚动到顶部
    scrollToTop() {
      // 获取组件容器元素
      const container = this.$el.querySelector('.el-card');
      if (container) {
        // 滚动到容器顶部
        container.scrollTop = 0;
      }
    },

    // 显示详情弹窗
    showDetailDialog(item) {
      this.detailItem = item; // 存储当前项用于弹窗显示
      this.dialogVisible = true; // 显示弹窗
    },

    // 关闭弹窗
    handleClose(done) {
      this.dialogVisible = false;
      done();
    },

    // 复制完整提示词
    async copyFullPrompt() {
      // 如果正在加载，则直接返回，不执行复制
      if (this.loading) {
        return;
      }
      this.loading = true; // 开始加载

      try {
        const fullPrompt = `正面提示词:\n${this.detailItem.prompt}\n\n负面提示词:\n${this.detailItem.negativePrompt}`;
        await navigator.clipboard.writeText(fullPrompt);
        this.$message.success("已复制到剪贴板");
      } catch (err) {
        console.error('复制失败:', err);
        this.$message.error("复制失败，请手动复制。");

      } finally {
        // 无论成功还是失败，都设置 loading 为 false
        // 延迟 0.5 秒再设置，模拟复制操作的耗时
        setTimeout(() => {
          this.loading = false;
        }, 500);
      }
    },

    // 监听滚动事件，控制回到顶部按钮显示
    handleScroll() {
      const container = this.$el.querySelector('.el-card');
      if (container) {
        // 当滚动距离大于200像素时显示回到顶部按钮
        this.showBackTop = container.scrollTop > 200;
      }
    }
  },
  mounted() {
    // 1. 生成分类选项列表
    // 从嵌套结构的键（分类名称）创建选项
    this.categories = [
      {label: '全部', value: ''},
      ...Object.keys(this.promptsData).map(key => ({label: key, value: key}))
    ];

    // 2. 初始化显示所有数据
    this.selectCategory(''); // 调用 selectCategory 以初始化显示所有数据

    // 3. 添加滚动监听
    const container = this.$el.querySelector('.el-card');
    if (container) {
      container.addEventListener('scroll', this.handleScroll);
    }
  },
  beforeDestroy() {
    // 组件销毁前移除滚动监听
    const container = this.$el.querySelector('.el-card');
    if (container) {
      container.removeEventListener('scroll', this.handleScroll);
    }
  }
};
</script>

<!-- 引入共享样式 -->
<style scoped lang="css" src="@/styles/shared-prompts.css"></style>
